import React, { useEffect } from 'react'
import $ from 'jquery'; 
export default function Navbar() {

    window.addEventListener('scroll',scrolling)
    function scrolling(){
      if($(window).scrollTop()>90){
        $('.navbarmain').addClass('navbarmain1')
      }
      else{
        $('.navbarmain').removeClass('navbarmain1')
      }
    }
   
    useEffect(function(){
      let bar=$('.bar')
      bar.on('click',slider)
      function slider(){
        $('.slidebar').css('transform','translateX(0px)')
      }
      $('.close').on('click',function(){
        $('.slidebar').css('transform','translateX(-900px)')
      })
    },)
  return (
    <div className='home'>
      <div className="slidebar">
        <h4 className='text-end px-3 py-3 close' >close</h4>
        <div className='navsmall'>
          <ul className='text-center'>
            <li>Home</li>
            <li>About</li>
            <li>Portfolio</li>
            <li>News</li>
            <li>Contact</li>
            <div className='py-3'>
            <a className='btn btn-dark text-white'>BUY NOW</a>
            </div>
           
          </ul>
        </div>
      </div>
      <div className="container">
        <div className="row py-3 navbarmain">
          <div className="col-lg-4  text-white hbox1">
            <img src='./images/t1.jpg' className='title'/>
          </div>
          <div className="col-lg-8 ">
            <ul className='text-white menu text-center'>
              <li><a>Home</a></li>
              <li><a>About</a></li>
              <li><a>Portfolio</a></li>
              <li><a>News</a></li>
              <li><a>Contact</a></li>
              <span className="mx-3"><a className='btn btn-primary'>BUY NOW</a></span>
              
            </ul>
          </div>
          <span className="bar">
            <div className="bar1"></div>
            <div className="bar1"></div>
            <div className="bar1"></div>
          </span>
        </div>
      </div>
      
    </div>
  )
}
