import React from 'react'
import 'aos';
import Aos from 'aos';
export default function About() {
  Aos.init();
  return (
    <div className='about bg-light py-5'>
      <div className="container">
        <div className="row">
          <div className="col-lg-6">
            <div className="box1 "  >
            <img src='/images/about.jpg' data-aos={"fade-up"}
              data-aos-duration="2000"></img>
            </div>
            
          </div>
          <div className="col-lg-5 text-start">
            <div className=" box2"   data-aos-duration="2000" data-aos={"fade-up"} >
            <a className='aboutme'>ABOUT ME</a>
            <h4 className='py-2 cindependent'>Creative Independent Web Developer based in USA</h4>
            <p>'m web designer, and I'm very passionate and dedicated to my work. With 20 years experience as a professional web developer, I have acquired the skills and knowledge necessary to make your project a success. 
              I enjoy every step of the design process, from discussion and collaboration.</p>
              <a className='btn dcv' data-aos="fade-up"
              data-aos-anchor-placement="top-bottom">Download CV</a>
            </div>
            
          </div>

        </div>
      </div>
    </div>
  )
}
