import $ from 'jquery';
export default function Galleries() {
    $(document).ready(function () {

        $(".filter-button").click(function () {
            var value = $(this).attr('data-filter');

            if (value == "all") {
                $('.filter').removeClass('hidden');
                $('.filter').show('1000');
            }
            else {
                $('.filter[filter-item="' + value + '"]').removeClass('hidden');
                $(".filter").not('.filter[filter-item="' + value + '"]').addClass('hidden');
                $(".filter").not('.' + value).hide('3000');
                $('.filter').filter('.' + value).show('3000');

            }
        });

        if ($(".filter-button").removeClass("active")) {
            $(this).removeClass("active");
        }
        $(this).addClass("active");

    });
    return (
        <div className="gallery bg-light py-4">
            <div className="container">
                <div className="row py-5">
                <div  className='col-lg-5 text-start px-5'>
                    <h1>Creative Portfolio</h1>
                </div>
                    <div  className='col-lg-5 '>
                        <button className="btn btn-default filter-button btn-dark" data-filter="all">All</button>
                        <button className="btn btn-default filter-button mx-3" data-filter="design">Design</button>
                        <button className="btn btn-default filter-button" data-filter="branding">Branding</button>
                        <button className="btn btn-default filter-button" data-filter="photo">Photography</button>
                    
                    </div>
                <br/>
                  
                    <div className="gallery_product col-lg-4  filter all design photo py-4">
                        <div className='gbox1'>
                        <img src="images/1.jpg" className="img-responsive" width='350px' height='350px'/>
                        <div className="box1 text-dark">
                            <div className="row">
                                <div className="col-lg-6">
                                    <h6><b>My Story</b></h6>
                                </div>
                                <div className="col-lg-6">
                                    <h6>Design</h6>
                                </div>
                            </div>
                        </div>
                        </div>
                        

                    </div>
                    <div className="gallery_product col-lg-4  filter all design branding py-4">
                      
                        <div className="gbox1">
                        <div className="box1  text-dark">
                            <div className="row">
                                <div className="col-lg-6">
                                    <h6><b>Design Trend</b></h6>
                                </div>
                                <div className="col-lg-6">
                                    <h6>Branding</h6>
                                </div>
                            </div>
                        </div>
                        <img src="images/2.jpg" className="img-responsive"  width='350px' height='350px' />
                        </div>
                       
                    </div>


                    <div className="gallery_product col-lg-4  filter photo  all py-4 branding design">
                    <div className="gbox1">
                        <div className="box1  text-dark">
                            <div className="row">
                                <div className="col-lg-6">
                                    <h6><b>My Story</b></h6>
                                </div>
                                <div className="col-lg-6">
                                    <h6>Design</h6>
                                </div>
                            </div>
                        </div>
                        <img src="images/3.jpg" className="img-responsive"  width='350px' height='350px' />
                    </div>
                    </div>
                    <div className="gallery_product col-lg-4  filter branding design py-4">
                       
                        <div className="gbox1">
                        <img src="images/l3.jpg" className="img-responsive"  width='350px' height='350px' />
                        <div className="box1  text-dark">
                            <div className="row">
                                <div className="col-lg-6">
                                    <h6><b>My Story</b></h6>
                                </div>
                                <div className="col-lg-6">
                                    <h6>Design</h6>
                                </div>
                            </div>
                        </div>
                        </div>
                    </div>
                    <div className="gallery_product col-lg-4  filter branding py-4">
                       
                        <div className="gbox1">
                        <img src="images/back3.jpg" className="img-responsive"  width='350px' height='350px' />
                        <div className="box1  text-dark">
                            <div className="row">
                                <div className="col-lg-6">
                                    <h6><b>My Story</b></h6>
                                </div>
                                <div className="col-lg-6">
                                    <h6>Photography</h6>
                                </div>
                            </div>
                        </div>
                        </div>
                    </div>
                </div>


            </div>
        </div>
    )
}